<template>
  <v-container class="grey lighten-4" fluid>
    <v-row cols="3">
      <v-col class="d-flex flex-column">
        <v-card>
          <v-card-title class="headline">
            {{ title }}
          </v-card-title>
          <v-card-text>
            <div>
              New UUID every
              <v-btn
                fab dark
                v-for="t in times"
                :key="t"
                color="primary"
                small
                class="mx-1"
                @click="setNewTime(t)"
                >{{ t }}</v-btn
              >
              seconds (default: <b>{{defaultValue}}</b>)
            </div>
            <div>
              <v-switch v-model="uuidUpperCase" label="Uppercase?" class="hundredpx"></v-switch>
            </div>
            <br />
            <div class="pt-2 d-flex align-content-space-between">
              <div>
                <v-card class="pa-2" outlined tile>
                  {{ uuidUpperCase ? guid.toUpperCase() : guid.toLowerCase() }}
                </v-card>
                <linear-progress-bar
                  @timer-elapsed="timerElapsed"
                  :max="time"
                ></linear-progress-bar>
              </div>
              <v-btn v-clipboard="uuidUpperCase ? guid.toUpperCase() : guid.toLowerCase()" class="ml-4 mt-1 primary">
                Copy
                <v-icon right size="17">mdi-content-copy</v-icon>
              </v-btn>
            </div>
            <div>
              <last-five-uuids class="mt-4" :uuids-list="uuidsList" :is-outlined="true" height="250" width="490"/>
            </div>
          </v-card-text>
        </v-card>
        <!-- <last-five-uuids class="mt-4" :uuids-list="uuidsList" height="250" width="490"/> -->
      </v-col>
      <v-col class="d-flex">
        <bulk-export />
      </v-col>
      <v-col>
        <v-card>
          <v-card-subtitle class="font-weight-bold">Why Us?</v-card-subtitle>
          <v-card-text>
            <why-us />
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <!-- <v-row>
      <br />
    </v-row> -->
    <v-row cols="3" class="mt-3 ml-0">
      <disclaimer />
    </v-row>
  </v-container>
</template>

<script>
import uuid from "uuid/v4";
import LinearProgressBar from "./LinearProgressBar";
import LastFiveUuids from "./LastFiveUuids";
import BulkExport from "./BulkExport";
import Disclaimer from "./Disclaimer";
import WhyUs from "./WhyUs";
// import AboutUs from "./AboutUs";

export default {
  name: "AutoGuid",
  components: {
    LinearProgressBar,
    LastFiveUuids,
    BulkExport,
    Disclaimer,
    WhyUs
    // AboutUs
  },
  metaInfo: {
    title: "Auto UUID generator and Bulk Export",
    meta: [
      { "http-equiv": "Content-Type", content: "text/html; charset=utf-8"},
      { name: "description", content: "Easy way to automatically generate universally unique identifiers (UUIDs) online either single or bulk. You can also export bunch of UUIDs to a CSV file."},
      { name: "keywords", content: "guid, uuid, guid online, guid generator, uuid online, uuid generator, onlineuuid, onlineuuid, universally unique identifier, js guid, auto guid generator, auto uuid generator, auto uuid, auto guid, unique identifier, unique id generator, auto unique id"},

      // OpenGraph data (Most widely used)
      {property: "og:title", content: "Auto UUID generator and Bulk Export"},
      {property: "og:site_name", content: "Auto UUID Generator"},
      // The list of types is available here: http://ogp.me/#types
      {property: "og:type", content: "website"},
      {property: "og:description", content: "Easy way to automatically generate universally unique identifiers (UUIDs) online either single or bulk. You can also export bunch of UUIDs to a CSV file."},

      // Twitter card
      {name: "twitter:card", content: "summary"},
      {name: "twitter:title", content: "Auto UUID generator and Bulk Export"},
      {name: "twitter:description", content: "Easy way to automatically generate universally unique identifiers (UUIDs) online either single or bulk. You can also export bunch of UUIDs to a CSV file."},
      // Your twitter handle, if you have one.
      {name: "twitter:creator", content: "@nani938"},

      // Google / Schema.org markup:
      { itemprop: "name", content: "Auto UUID generator and Bulk Export" },
      { itemprop: "description", content: "Easy way to automatically generate universally unique identifiers (UUIDs) online either single or bulk. You can also export bunch of UUIDs to a CSV file." },
    ]
    // https://www.digitalocean.com/community/tutorials/vuejs-vue-seo-tips
  },
  data() {
    return {
      title: "Auto UUID generator",
      guid: uuid(),
      time: 5,
      times: [3, 5, 10, 15],
      uuidsList: [],
      uuidUpperCase: false,
      defaultValue: 5,
      clicked: false
    };
  },
  methods: {
    timerElapsed() {
      this.adjustUuidsList();
      this.guid = this.uuidUpperCase
        ? uuid().toUpperCase()
        : uuid().toLowerCase();
    },
    adjustUuidsList() {
      const formattedId = this.uuidUpperCase
        ? this.guid.toUpperCase()
        : this.guid.toLowerCase();
      if (this.uuidsList.length > 4) {
        this.uuidsList.shift();
        this.uuidsList.push(formattedId);
      } else {
        this.uuidsList.push(formattedId);
      }
    },
    setNewTime(n) {
      this.time = Number(n);
    }
  }
};
</script>
<style scoped lang="scss">
.hundredpx {
  width: 100px;
}
</style>
